import React from "react";
import { Link } from "react-router-dom";
import { useAuth } from "../../services/auth/authProvider";

function Nav() {
  const auth = useAuth();
  return (
    <header className="header flex items-center px-8 py-3 gap-8 bg-white shadow">
      {/* Left: Logo */}
      <div className="flex items-start gap-4 flex-shrink-0 min-w-[180px]">
        <button>
          <img
            src={require("../../assets/images/contacts_logo.png")}
            alt="logo"
            className="h-10 w-10 object-contain"
            onClick={() => {
              window.location.href = "/Home";
            }}
          />
        </button>
        <span className="header-title hidden sm:inline text-2xl font-bold text-blue-700">
          SumPerks
        </span>
      </div>
      {/* Center: Navigation Links */}
      <nav className="flex-1 flex justify-left gap-6">
        {auth.loggedIn() ? (
          <>
            <Link
              to="/Home"
              className="text-blue-700 font-semibold hover:underline transition"
            >
              Home
            </Link>
            <Link
              to="/Search"
              className="text-blue-700 font-semibold hover:underline transition"
            >
              Search
            </Link>
          </>
        ) : null}
      </nav>
      {/* Right: Auth/User */}
      <div className="flex items-center gap-4 flex-shrink-0 min-w-[180px] justify-end">
        {auth.loggedIn() ? (
          <>
            <span className="text-gray-600 font-medium whitespace-nowrap">
              Welcome {auth.user}
            </span>
            <button
              className="px-4 py-1 bg-blue-600 text-white rounded hover:bg-blue-700 transition font-semibold"
              onClick={() => {
                auth.logout();
              }}
            >
              Log out
            </button>
          </>
        ) : null}
      </div>
    </header>
  );
}

export default Nav;
