import ContactCards from "./contactCards";
import { useEffect } from "react";
import { useAuth } from "../../services/auth/authProvider";
import TextField from "@mui/material/TextField";
import { useSelector, useDispatch } from "react-redux";
import { RootState } from "../../store/store";
import {
  setAddress,
  setContactList,
  setDateOfBirth,
  setFilterQuery,
  setId,
  setProfileImage,
  setUserName,
} from "./contactSlice";

function Search() {
  const url = process.env.REACT_APP_API_SERVICE_URL + "/api/person/";
  //const [contactList, setContactList]: any = useState();
  //const [filterQuery, setFilterQuery]: any = useState("");
  const auth: any = useAuth();

  const dispatch = useDispatch();
  const contactList = useSelector(
    (state: RootState) => state.contact.contactList
  );
  const filterQuery = useSelector(
    (state: RootState) => state.contact.filterQuery
  );

  useEffect(() => {
    const fetchData = async () => {
      debugger;
      const requestOptions = {
        method: "GET",
      };
      const response = await auth.fetchApi(url, requestOptions);
      dispatch(setId(response.id));
      dispatch(setUserName(response.name));
      dispatch(setDateOfBirth(response.dateOfBirth));
      dispatch(setAddress(response.address));
      dispatch(setProfileImage(response.imagePath));
      dispatch(setContactList(response));
    };
    fetchData();
  }, [auth, dispatch, url]);

  debugger;
  const filteredContacts =
    filterQuery.length === 0
      ? contactList
      : contactList.filter((contact: any) =>
          contact.name.toLowerCase().includes(filterQuery.toLowerCase())
        );

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-gray-100 flex flex-col items-center py-10">
      <section className="w-full max-w-screen-lg mb-6">
        <form className="bg-white rounded-2xl shadow p-6 flex flex-col items-center">
          <TextField
            id="outlined-basic"
            variant="outlined"
            fullWidth
            label="Search here"
            value={filterQuery}
            onChange={(e) => dispatch(setFilterQuery(e.target.value))}
          />
        </form>
      </section>
      {/* Results */}
      <section className="w-full max-w-6xl">
        {filteredContacts?.length ? (
          <ContactCards contactList={filteredContacts} />
        ) : (
          <div className="bg-white rounded-xl shadow p-6 text-center text-gray-500">
            No data matches your search
          </div>
        )}
      </section>
    </div>
  );
}

export default Search;
