import { Route, Routes } from "react-router-dom";

import "./App.css";

import Home from "./features/home/home";
import Login from "./features/login/login";
import PrivateRoute from "./routes/privateRoute";
import Search from "./features/contact/search";
import ViewContact from "./features/contact/viewContact";
import AddContact from "./features/contact/addContact";
import Nav from "./layouts/header/nav";
import AuthProvider from "./services/auth/authProvider";
import MainLayout from "./layouts/MainLayout";

const App = () => {
  return (
    <AuthProvider>
      <Nav />
      <Routes>
        {/* Public routes */}
        <Route path="/" element={<Home />} />
        <Route path="/Login" element={<Login />} />

        {/* All main app pages use MainLayout */}
        <Route element={<MainLayout />}>
          {/* Protected routes */}
          <Route element={<PrivateRoute />}>
            <Route path="/Home" element={<Home />} />
            <Route path="/Search" element={<Search />} />
            <Route path="/ViewContact" element={<ViewContact />} />
            <Route path="/AddContact" element={<AddContact />} />
            {/* Add more protected routes here */}
          </Route>
          {/* You can add non-protected routes here if needed */}
        </Route>
      </Routes>
    </AuthProvider>
  );
};
export default App;
