import { useEffect } from "react";
import { useAuth } from "../../services/auth/authProvider";
import { useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { RootState } from "../../store/store";
import { setId } from "./contactSlice";

function ViewContact() {
  const auth = useAuth();
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const contact = {
    id: useSelector((state: RootState) => state.contact.id),
    userName: useSelector((state: RootState) => state.contact.userName),
    dateOfBirth: useSelector((state: RootState) => state.contact.dateOfBirth),
    address: useSelector((state: RootState) => state.contact.address),
    profileImage: useSelector((state: RootState) => state.contact.profileImage),
  };

  useEffect(() => {
    const fetchData = async () => {
      debugger;
      const requestOptions = { method: "GET" };
      const resp = await auth.fetchApi(
        process.env.REACT_APP_API_SERVICE_URL + "/api/person/" + contact.id,
        requestOptions
      );
      dispatch(setId(resp.id));
    };
    fetchData();
  }, [auth, contact.id, dispatch]);

  async function handleContact(event: any) {
    if (event.target.getAttribute("name") === "delete") {
      const requestOptions = { method: "DELETE" };
      await auth.fetchApi(
        process.env.REACT_APP_API_SERVICE_URL + "/api/person/" + contact.id,
        requestOptions
      );
      navigate("/Search");
    } else {
      navigate("/AddContact", { state: { id: contact.id } });
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-gray-100 flex items-center justify-center px-2">
      <div className="bg-white rounded-2xl shadow-xl p-6 sm:p-10 flex flex-col items-center w-full max-w-xs sm:max-w-md md:max-w-lg">
        <div className="w-28 h-28 sm:w-32 sm:h-32 rounded-full bg-gray-100 border-4 border-blue-200 shadow flex items-center justify-center overflow-hidden mb-4">
          {contact.profileImage ? (
            <img
              src={
                process.env.REACT_APP_API_SERVICE_URL +
                "\\" +
                contact.profileImage
              }
              alt="Profile"
              className="w-28 h-28 object-cover"
            />
          ) : (
            <span className="text-gray-400 text-4xl">?</span>
          )}
        </div>
        <h2 className="text-2xl font-bold text-blue-700 mb-2 text-center">
          {contact.userName}
        </h2>
        <p className="text-gray-600 text-center mb-1">
          <span className="font-medium">DOB:</span> {contact.dateOfBirth}
        </p>
        <p className="text-gray-500 text-center">
          <span className="font-medium">Address:</span> {contact.address}
        </p>
        <div className="flex justify-end space-x-4 mt-4 w-full">
          <button
            id={contact.id}
            name="edit"
            onClick={handleContact}
            className="px-5 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition font-semibold w-full"
          >
            Edit
          </button>
          <button
            id={contact.id}
            name="delete"
            onClick={handleContact}
            className="px-5 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition font-semibold w-full"
          >
            Delete
          </button>
        </div>
      </div>
    </div>
  );
}

export default ViewContact;
