import { useNavigate } from "react-router-dom";
import { useAuth } from "../../services/auth/authProvider";
import AddContact from "../contact/addContact";

function Home() {
  const navigate = useNavigate();
  const auth = useAuth();

  return (
    <>
      {auth.loggedIn() ? (
        <AddContact />
      ) : (
        <div className="min-h-screen bg-gradient-to-br from-blue-50 to-gray-100 flex items-center justify-center px-2">
          <div className="bg-white rounded-2xl shadow-xl p-6 sm:p-8 flex flex-col items-center w-full max-w-xs sm:max-w-md md:max-w-lg">
            <img
              src={require("../../assets/images/networking7.avif")}
              alt="logo"
              className="mb-6 w-32 h-32 sm:w-40 sm:h-40 object-contain"
            />
            <h1 className="text-2xl sm:text-3xl font-extrabold text-blue-700 mb-6 text-center">
              Welcome!
            </h1>
            <p className="text-base sm:text-lg text-gray-600 text-center mb-8">
              It is a long established fact that a reader will be distracted by
              the readable content of a page when looking at its layout. The
              point of using Lorem Ipsum is that it has a more-or-less normal
              distribution of letters, as opposed to using 'Content here,
              content here', making it look like readable English.
            </p>
            <button
              onClick={() => navigate("/Login")}
              className="px-6 py-2 bg-blue-600 text-white rounded-lg shadow hover:bg-blue-700 transition font-semibold w-full sm:w-auto"
            >
              Log in
            </button>
          </div>
        </div>
      )}
    </>
  );
}

export default Home;
