import "../../styles/contactsCards.css";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import {
  setUserName,
  setDateOfBirth,
  setAddress,
  setProfileImage,
  setId,
} from "./contactSlice";

function ContactCards({ contactList }) {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  function handleContact(contact: any) {
    dispatch(setId(contact.id));
    dispatch(setUserName(contact.name));
    dispatch(setDateOfBirth(contact.dateOfBirth));
    dispatch(setAddress(contact.address));
    dispatch(setProfileImage(contact.imagePath));
    navigate("/ViewContact");
  }

  return (
    <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6 w-full">
      {contactList?.map((contact, index) => (
        <figure
          className="bg-white rounded-xl shadow p-6 flex flex-col items-center cursor-pointer hover:shadow-lg transition"
          key={index}
          onMouseDown={() => handleContact(contact)}
        >
          <img
            alt="user"
            src={
              process.env.REACT_APP_S3_ENABLED === "true"
                ? contact.itemUrl
                : process.env.REACT_APP_API_SERVICE_URL +
                  "\\" +
                  contact.imagePath
            }
            className="w-20 h-20 rounded-full object-cover mb-4"
          />
          <figcaption className="text-center">
            <p className="font-bold text-blue-700 mb-1">{contact.name}</p>
            <p className="text-gray-600 text-sm mb-1">
              <span className="font-medium">DOB:</span> {contact.dateOfBirth}
            </p>
            <p className="text-gray-500 text-sm">
              <span className="font-medium">City:</span> {contact.address}
            </p>
          </figcaption>
        </figure>
      ))}
    </div>
  );
}

export default ContactCards;
