import { FormEvent, useEffect, useRef } from "react";
import "../../styles/addContact.css";
import { useAuth } from "../../services/auth/authProvider";
import { useLocation, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { RootState } from "../../store/store";
import { resetContact } from "./contactSlice";

import {
  setUserName,
  setDateOfBirth,
  setAddress,
  setProfileImage,
  setId,
} from "./contactSlice";

function AddContact() {
  const fileInputRef = useRef<HTMLInputElement>(null);
  const auth = useAuth();
  const navigation = useNavigate();
  const location = useLocation();

  const dispatch = useDispatch();
  const userName = useSelector((state: RootState) => state.contact.userName);
  const dateOfBirth = useSelector(
    (state: RootState) => state.contact.dateOfBirth
  );
  const address = useSelector((state: RootState) => state.contact.address);
  const profileImage = useSelector(
    (state: RootState) => state.contact.profileImage
  );

  //const id = useSelector((state: RootState) => state.contact.id);
  const id = location.state?.id;

  // const [newUserName, setNewUserName] = useState(userName);
  // const [newDateOfBirth, setNewDateOfBirth] = useState(dateOfBirth);
  // const [newAddress, setNewAddress] = useState(address);
  // const [newProfileImage, setNewProfileImage] = useState<File | null>(
  //   profileImage ? (profileImage as unknown as File) : null
  // );

  useEffect(() => {
    dispatch(resetContact());

    const fetchData = async () => {
      const requestOptions = {
        method: "GET",
      };
      const resp = await auth.fetchApi(
        process.env.REACT_APP_API_SERVICE_URL + "/api/person/" + id,
        requestOptions
      );

      dispatch(setUserName(resp.name));
      dispatch(setDateOfBirth(resp.dateOfBirth));
      dispatch(setAddress(resp.address));
      dispatch(setProfileImage(resp.imagePath));
    };
    if (id) fetchData();
  }, [auth, id, dispatch]);

  async function handleSubmitEvent(
    event: FormEvent<HTMLFormElement>
  ): Promise<void> {
    const formData = new FormData(event.currentTarget);
    debugger;
    event.preventDefault();
    dispatch(setId(formData.get("id") as string));
    dispatch(setUserName(formData.get("UserName") as string));
    dispatch(setDateOfBirth(formData.get("DateOfBirth") as string));
    dispatch(setAddress(formData.get("address") as string));
    dispatch(
      setProfileImage(profileImage ? profileImage : "No Image attached.")
    );

    const requestOptions = {
      method: id ? "PUT" : "POST",
      body: JSON.stringify({
        name: userName,
        dateOfBirth: dateOfBirth,
        address: address,
        ImagePath: profileImage,
      }),
    };

    if (!auth.isTokenExpired(auth.token)) {
      const url = process.env.REACT_APP_API_SERVICE_URL + "/api/person/";
      await auth.fetchApi(id ? url + id : url, requestOptions, false);
      navigation("/Search");
    }
  }

  async function handleFile(event) {
    let fileToUpload = event.target.files[0] as File;
    const formData = new FormData();
    formData.append("file", fileToUpload, fileToUpload.name);

    const requestOptions = {
      method: id ? "PUT" : "POST",
      body: formData,
    };

    if (!auth.isTokenExpired(auth.token)) {
      const url = process.env.REACT_APP_API_SERVICE_URL + "/api/upload/";
      const resp = await auth.fetchApi(url, requestOptions, true);
      dispatch(
        setProfileImage(
          process.env.REACT_APP_S3_ENABLED === "true"
            ? resp.itemUrl
            : resp.dbPath
        )
      );
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-gray-100 flex items-center justify-center px-2">
      <form
        onSubmit={handleSubmitEvent}
        className="bg-white rounded-2xl shadow-xl p-4 sm:p-8 flex flex-col items-center w-full max-w-xs sm:max-w-md md:max-w-lg"
      >
        {/* Profile Image Avatar */}
        <div className="mb-6 flex flex-col items-center">
          <div
            className="w-20 h-20 sm:w-24 sm:h-24 rounded-full bg-gray-100 border-4 border-blue-200 shadow flex items-center justify-center overflow-hidden mb-2 cursor-pointer hover:ring-2 hover:ring-blue-400 transition"
            onClick={() => fileInputRef.current?.click()}
            title="Click to upload profile image"
          >
            {profileImage ? (
              <img
                src={
                  process.env.REACT_APP_API_SERVICE_URL + "\\" + profileImage
                }
                alt="Profile"
                className="w-28 h-28 object-cover"
              />
            ) : (
              <span className="text-gray-400 text-3xl sm:text-4xl">+</span>
            )}
          </div>
          <span className="text-xs sm:text-sm text-gray-500">
            Profile Image
          </span>
          {/* Hidden file input */}
          <input
            ref={fileInputRef}
            type="file"
            accept="image/*"
            className="hidden"
            onChange={handleFile}
          />
        </div>
        <h2 className="text-xl sm:text-2xl font-bold text-blue-700 mb-6 text-center">
          {location?.state ? "Update Contact" : "Add Contact"}
        </h2>
        <div className="w-full mb-4">
          <label className="block text-gray-700 mb-1 text-left text-sm">
            Name
          </label>
          <input
            type="text"
            className="w-full px-3 py-2 sm:px-4 sm:py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400 text-sm sm:text-base"
            value={userName}
            onChange={(e) => dispatch(setUserName(e.target.value))}
            required
          />
        </div>
        <div className="w-full mb-4">
          <label className="block text-gray-700 mb-1 text-left text-sm">
            Date of Birth
          </label>
          <input
            type="date"
            className="w-full px-3 py-2 sm:px-4 sm:py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400 text-sm sm:text-base"
            value={dateOfBirth}
            onChange={(e) => dispatch(setDateOfBirth(e.target.value))}
            required
          />
        </div>
        <div className="w-full mb-4">
          <label className="block text-gray-700 mb-1 text-left text-sm">
            Address
          </label>
          <input
            type="text"
            className="w-full px-3 py-2 sm:px-4 sm:py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400 text-sm sm:text-base"
            value={address}
            onChange={(e) => dispatch(setAddress(e.target.value))}
            required
          />
        </div>
        <button
          type="submit"
          className="w-full py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition font-semibold text-sm sm:text-base"
        >
          {location?.state ? "Update Contact" : "Add Contact"}
        </button>
      </form>
    </div>
  );
}

export default AddContact;
