import { Outlet, Link } from "react-router-dom";
import { useState } from "react";
import { HiMenuAlt2, HiChevronLeft } from "react-icons/hi";

const menuItems = [
  { label: "Dashboard", path: "/Home" },
  { label: "Search Contacts", path: "/Search" },
  { label: "Add Contact", path: "/AddContact" },
  { label: "Settings", path: "/Settings" },
];

function MainLayout() {
  const [collapsed, setCollapsed] = useState(false);

  return (
    <div className="min-h-screen flex bg-gradient-to-br from-blue-50 to-gray-100">
      {/* Sidebar */}
      <aside
        className={`bg-white shadow-lg p-4 flex flex-col gap-4 transition-all duration-300 ${
          collapsed ? "w-16" : "w-64"
        }`}
      >
        <button
          className="mb-4 self-end text-blue-700 focus:outline-none"
          onClick={() => setCollapsed((c) => !c)}
          title={collapsed ? "Expand menu" : "Collapse menu"}
        >
          {collapsed ? <HiMenuAlt2 size={24} /> : <HiChevronLeft size={24} />}
        </button>
        {!collapsed && (
          <>
            <h2 className="text-xl font-bold text-blue-700 mb-4">Menu</h2>
            <nav className="flex flex-col gap-2">
              {menuItems.map((item) => (
                <Link
                  key={item.path}
                  to={item.path}
                  className="text-gray-700 hover:text-blue-600 font-medium transition"
                >
                  {item.label}
                </Link>
              ))}
            </nav>
          </>
        )}
        {collapsed && (
          <nav className="flex flex-col gap-4 items-center">
            {menuItems.map((item, idx) => (
              <Link
                key={item.path}
                to={item.path}
                className="text-gray-400 hover:text-blue-600 font-bold text-lg"
                title={item.label}
              >
                {item.label[0]}
              </Link>
            ))}
          </nav>
        )}
      </aside>
      {/* Main Content */}
      <main className="flex-1 p-8">
        {/* <Breadcrumb /> */}
        <Outlet />
      </main>
    </div>
  );
}

export default MainLayout;
