import { jwtDecode } from "jwt-decode";
import { useContext, createContext, ReactNode, useState } from "react";
import { useNavigate } from "react-router-dom";

const AuthContext = createContext(null);
interface Props {
  children?: ReactNode;
}

export default function AuthProvider({ children }: Props) {
  const [user, setUser] = useState(null);
  const domain = process.env.REACT_APP_API_SERVICE_URL; // API server domain
  const navigate = useNavigate();
  const login = async (UserName: any, Password: any) => {
    // Get a token from api server using the fetch api
    const res = await fetchApi(
      process.env.REACT_APP_API_SERVICE_URL + "/api/auth/login",
      {
        method: "POST",
        body: JSON.stringify({
          UserName,
          Password,
        }),
      },
      null
    );
    setToken(res.token); // Setting the token in localStorage
    setRefreshToken(res.refreshToken);
    setUser(res.user);
    return await Promise.resolve(res);
  };

  const loggedIn = () => {
    // Checks if there is a saved token and it's still valid
    const token = getToken(); // Getting token from localstorage
    return !!token && !isTokenExpired(token); // handwaiving here
  };

  const isTokenExpired = (token: string) => {
    try {
      const decoded = jwtDecode(token);
      if (decoded.exp < Date.now() / 1000) {
        // Checking if token is expired.
        return true;
      } else return false;
    } catch (err) {
      console.log("expired check failed! Line 42: AuthService.js");
      return false;
    }
  };

  const setToken = (idToken: string) => {
    // Saves user token to localStorage
    localStorage.setItem("id_token", idToken);
  };
  const setRefreshToken = (refreshToken: string) => {
    // Saves user refresh token to localStorage
    localStorage.setItem("refreshToken", refreshToken);
  };

  const getToken = () => {
    // Retrieves the user token from localStorage
    return localStorage.getItem("id_token");
  };

  const getrefreshToken = () => {
    // Retrieves the user refresh token from localStorage
    return localStorage.getItem("refreshToken");
  };

  const logout = () => {
    // Clear user token and profile data from localStorage
    localStorage.removeItem("id_token");
    setUser("");
    navigate("/");
  };

  const getConfirm = () => {
    // Using jwt-decode npm package to decode the token
    let answer = jwtDecode(getToken());
    console.log("Received answer!");
    return answer;
  };

  const fetchApi = async (
    url: string | URL | Request,
    options: RequestInit | undefined,
    isFile: null
  ) => {
    let headers = {};
    // performs api calls sending the required authentication headers
    if (!isFile) {
      headers = {
        Accept: "application/json",
        "Content-Type": "application/json;",
      };
    }
    // Setting Authorization header
    // Authorization: Bearer xxxxxxx.xxxxxxxx.xxxxxx
    if (loggedIn()) {
      headers["Authorization"] = "Bearer " + getToken();
    }

    const response = await fetch(url, {
      headers,
      ...options,
    });
    const response_2 = _checkStatus(response);
    return response_2;
  };

  const _checkStatus = (response: {
    [x: string]: any;
    status: number;
    statusText: string | undefined;
  }) => {
    // raises an error in case response status is not a success
    if (response.status >= 200 && response.status < 300) {
      // Success status lies between 200 to 300
      if (response.status === 204) {
        return {};
      }
      return response.json();
    } else if (response.status === 401) {
      debugger;
      fetchApi(
        process.env.REACT_APP_API_SERVICE_URL + "/api/person/",
        {
          method: "GET",
        },
        null
      );
    } else if (response.status === 200) {
      return response;
    } else {
      var error = new Error(response.statusText);
      throw error;
    }
  };

  const RefreshToken = (token: string, refreshToken: string) => {
    if (isTokenExpired(token)) {
      debugger;
      fetchApi(
        domain + "/api/token/refresh",
        {
          method: "POST",
          body: JSON.stringify({
            AccessToken: token,
            RefreshToken: refreshToken,
          }),
          redirect: "follow",
        },
        null
      ).then((res) => {
        setToken(res.token); // Setting the token in localStorage
        return Promise.resolve(res);
      });
    }
  };

  return (
    <AuthContext.Provider
      value={{
        login,
        loggedIn,
        user,
        isTokenExpired,
        getToken,
        getrefreshToken,
        logout,
        getConfirm,
        fetchApi,
        _checkStatus,
        RefreshToken,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  return useContext(AuthContext);
}
